import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Checkinfo from '../views/Home/Checkinfo.vue'

import Workbeach from '../views/Workbeach.vue'

import Mine from '../views/Mine.vue'
import Message from '../views/Message/Message'

const routes = [
  {
    path: '/',
    redirect: '/Home'
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home,
    // redirect: '/Checkinfo',
    children:
      [
        {
          path: 'Checkinfo',
          name: 'Checkinfo',
          component: Checkinfo,
          meta: { transition: 'slide-right' }
        }
      ]
  },
  {
    path: '/Home/Checkinfo',
    name: 'Checkinfo',
    component: Checkinfo,
    meta: {
      transition: 'slide-right',
      title: '消息查询'
    }
  },
  {
    path: '/Message',
    name: 'Message',
    component: Message,
    meta: {
      title: '消息'
    }
  },
  {
    path: '/Workbeach',
    name: 'Workbeach',
    component: Workbeach,
    meta: {
      title: '工作台'
    }
  },
  {
    path: '/Mine',
    name: 'Mine',
    component: Mine,
    meta: {
      title: '我的'
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
