<template>
    <div class="itemWarp flex_mid" @click='changePage'>
        <span v-show='!bol'>
            <img :src='normalimage'>
        </span>
        <span v-show='bol'>
            <img :src='activeimage'>
        </span>
        <span class="normalText" v-show='!bol' v-text='txt'></span>
        <span class="selectText" v-show='bol' v-text='txt'></span>
    </div>
</template>
<script>
export default {
  name: 'Item',
  props: {
    txt: {
      type: String
    },
    page: {
      type: String
    },
    normalimage: String,
    activeimage: String,
    sel: {
      type: String
    }
  },
  computed: {
    bol: function () {
      if (this.sel === this.page) {
        return true
      }
      return false
    }
  },
  methods: {
    changePage: function () {
      // 点击跳转对应的页面
      this.$router.push('/' + this.page)
      this.$emit('change', this.page)
    }
  }
}
</script>
<style>
    .itemWarp{
        flex-grow: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    .itemWarp .normalText{
        font-size: 11px;
        color: #BCBCBC;
    }
    .itemWarp .selectText{
        font-size: 11px;
        color: #E7172B;
    }

    .itemWarp img{
        width: 20px;
        height: 20px;
